html {
  font-size: 20px !important;
}

body {
  font-size: 1rem;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: #000000;
}

h2 {
  font-weight: 900;
  font-size: 2rem;
  padding: 3.15rem 0 1.1rem 0;
  text-align: center;
}

ul {
  margin: 0 !important;
  list-style: none;
  padding: 0 !important;
}

li {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

button {
  outline: none;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

section {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-self: center;
}

/* ==========================     폰트      ===================== */
@font-face {
  font-family: "Gmarket-Bold";
